<template>
    <div class="issue">
        <div class="main">
            <div class="pack">
                <div class="warp">
                    <van-col span="12">
                        <p class="title">小梦想互助期号</p>
                    </van-col>
                    <van-col span="12" style="text-align:right" @click="selectPid">
                        <span style="color:rgb(153, 153, 153)">{{pid}}&nbsp;</span>
                        <van-icon name="arrow-down" style="font-size:0.3rem" @click.stop="selectPid"></van-icon>
                        <span class="last">
                            期
                        </span>
                    </van-col>
                </div>
            </div>
            <div class="pack">
                <div class="warp">
                    <van-col span="12">
                        <p class="title">梦想红包个数</p>
                    </van-col>
                    <van-col span="12" style="text-align:right">
                        <input type="number" class="people" v-model="pcount" placeholder="填写个数" style="width: 80%;text-align: right;color: #999;font-size: 0.3rem;">
                        <span class="last">
                            个
                        </span>
                    </van-col>
                </div>
            </div>
            <p class="tip">
                注：每个人只可以领取1个红包，红包里面有1个抓阄编号，抓阄成为幸运者可以获得大额梦想金！
            </p>
            <div class="textarea">
                <input type="text" placeholder="祝梦想成真！" v-model="content" class="blessing">
            </div>
            <div class="money">
                <span>￥</span>
                <span style="font-style:normal">{{pcount == '' ? 0 : pcount * 5}}</span>
            </div>
            <div class="btn">
                <van-button round size="large" style="color: white;font-size: 0.36rem;background-color: red;border: none;" @click="pay">微信支付</van-button>
            </div>
            <p class="msg">* 未领取的红包，在互助期结束后24小时内自动退款。</p>
        </div>
        <van-actionsheet
            v-model="show"
            :actions="action"
            cancel-text="取消"
            @select="onSelect"
            @cancel="onCancel"
        />
    </div>
</template>

<script>
export default {
    name:'issue',
    data () {
        return {
            show:false,
            action:[],//梦想期号列表
            pid:'',//梦想池 id
            pcount:'',//红包个数
            content:'',//祝福语
        }
    },
    created(){
        this.getPid(this)
    },
    methods:{
        selectPid(){
            this.show = true;
        },
        // 获取梦想池
        getPid(app){
            TD_Request('ds','plistr',{seek:0,count:100,redpack:true},function(code,data){
                console.log(data)
                $.each(data.Pools,function(index,item){
                    if(item.state == 'RUNNING'){
                        app.action.push({name:item.pid});
                    }
                })
                console.log(app.action)
                app.pid = app.action[0].name;
            },function(code,data){
                console.log(data)
            })
        },
        onSelect(item){
            this.show = false;
            this.pid = item.name;
        },
        onCancel(){
            this.show = false;
        },
        // 准备支付
        pay(){
            if(this.pid == ''){
                this.$dialog.alert({
                    title:'温馨提示',
                    message:'请填写梦想池编号'
                })
            }else if(this.pcount == ''){
                this.$dialog.alert({
                    title:'温馨提示',
                    message:'请填写红包个数'
                })
            }else if(typeof this.pcount != 'number'){
                console.log(typeof this.pcount != 'number')
                this.$dialog.alert({
                    title:'温馨提示',
                    message:'红包个数应为数字'
                })
            }else {
                this.rpPay(this)
            }
        },
        // 统一下单
        rpPay(app){
            if(this.content == ''){
                this.count = '祝梦想成真！'
            }
            TD_Request('rp','crp',{pid:app.pid,rcount:app.pcount,content:app.content,bill:0.01 * 100 * app.pcount,uid:app.$store.state.uid},function(code,data){
                console.log(data)
                app.wxpay(app,data);
            },function(code,data){
                if(code == 11){
                    app.$dialog.alert({
                        title:'绑定手机',
                        message:'您尚未绑定手机，绑定手机后即可发放红包'
                    }).then(()=>{
                        SaveStorage('share','issue');
                        app.router.push('/phone')
                    })
                }else if(code == 5){
                    app.$dialog.alert({
                        title:'温馨提示',
                        message:'该梦想池已结束'
                    })
                }else if(code == 67){
                    app.$dialog.alert({
                        title:'温馨提示',
                        message:'红包不能超过200个'
                    })
                }else if(code ==69){
                    app.$dialog.alert({
                        title:'温馨提示',
                        message:'您所支付的红包数量超出该期梦想池上限'
                    })
                }else if(code == 68){
                    app.$dialog.alert({
                        title:'温馨提示',
                        message:'系统订单错误，请稍后重试'
                    })
                }else if(code == 70){
                    app.$dialog.alert({
                        title:'温馨提示',
                        message:'红包创建错误'
                    })
                }
            })
        },
        // 微信支付&唤醒支付
        wxpay(app,res){
            WeixinJSBridge.invoke(
            'getBrandWCPayRequest', {
            "appId":res.order.appId,     //公众号名称，由商户传入     
            "timeStamp":res.order.timeStamp,         //时间戳，自1970年以来的秒数     
            "nonceStr":res.order.nonceStr, //随机串     
            "package":res.order.package,     
            "signType":res.order.signType,         //微信签名方式：     
            "paySign":res.order.paySign //微信签名 
            },function(res){
            if(res.err_msg == "get_brand_wcpay_request:ok" ){
            // 使用以上方式判断前端返回,微信团队郑重提示：
                    //res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
                app.paySuccess(app,res.rid)
            } else if(res.err_msg == "get_brand_wcpay_request:cancel"){
                app.$toast.fail('支付取消');
                app.content = '';
            }
        });
        },
        // 创建红包
        paySuccess(app,rid){
            TD_Request('rp','cprs',{uid:app.$store.state.uid,rid:rid},function(code,data){
                console.log(data)
                app.$router.push('/rpshare/'+rid);
            },function(code,data){
                console.log(data)
            })
        }
    },
    watch:{
        pcount(data){
            this.pcount = parseInt(data)
            if(data > 200){
                this.$dialog.alert({
                    title:'温馨提示',
                    message:'单笔红包不能超过200个'
                }).then(()=>{
                    this.pcount=''
                })
            }
        },
        content(data){
            if(data.length == 10){
                this.content = data.substr(0.10);
            }
        }
    }
}
</script>

