            padding-top: 0.3rem;
            .pack{
                height: 0.88rem;
                line-height: 0.88rem;
                background: #fff;
                border-radius: 0.1rem;
                margin-bottom: 0.4rem;
                .warp{
                    width: 6.3rem;
                    margin: 0 auto;
                    overflow: hidden;
                    .title{
                        font-size: 0.3rem;
                    }
                    span{
                        color: #999;
                        font-size: 0.3rem;
                    }
                    .last{
                        color: #000;
                    }
                }
                input{
                    position: relative;
                    top: -2px;
                }
            }
            .tip{
                font-size: 0.26rem;
                color: #333;
                width: 6.3rem;
                margin: 0 auto;
            }
            .textarea{
                margin-top: 0.6rem;
                height: 1.28rem;
