                margin-bottom: 1.2rem;
                background: #fff;
                border-radius: 0.1rem;
                .blessing{
                    width: 6.3rem;
                    outline: 0;
                    padding: 0 0.3rem;
                    font-size: 0.34rem;
                    color: #999;
                }
            }
            .money{
                text-align: center;
                font-size: 0.72rem;
